.nav {
    position: fixed;
    top: 0;
    left: 2rem;
    width: 95%;
    height: 8.75vh;
    z-index: 999999;
    border-radius: 10%;
    margin-top: 2rem;

}

.nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0rem 2.5rem;
    background: rgba(249, 245, 239, 0.2);
    border-radius: 1rem;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(7px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(249, 245, 239, 0.3);
}

.nav__left {
    display: flex;
    align-items: flex-start;
    border-right: 1px solid var(--nav-overlay-color);
}

.nav__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 22vw;
    height: 4.2rem;
}

.nav__logo h1 {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--title-color);
    font-family: "Expletus Sans";
    text-transform: uppercase;
    /* font-family: "Unbounded"; */
    /* font-family: "Aboreto"; */
}

.nav__logo img {
    width: 5.5rem;
    height: 5.5rem;
}

.nav__center {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 45vw
}

.nav__center ul {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.nav__center ul li {
    list-style: none;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    display: flex;
    align-items: center;
}

.nav__center ul li i {

    font-size: 1rem;
    color: var(--title-color);
}



.nav__center ul li a {
    text-decoration: none;
    color: var(--title-color);
    font-size: 1.5rem;
    font-weight: 700;
    font-family: "Aboreto";

}

#sale a {
    color: #ff3e3e
}

.nav__right {
    display: flex;
    align-items: center;
    width: 7vw;
    justify-content: flex-end;
    gap: 1rem;
    border-left: 1px solid #000;
    height: 4.2rem;
}

.nav__right i {
    font-size: 1.5rem;
    color: var(--title-color);
}
